.infoBox {
  flex: 1;
}

.infoBox:not(:last-child) {
  margin-right: 15px;
}

.infoBox-select {
  border-top: 10px solid greenyellow;
}

.redc {
  border-color: red;
}

.notredc {
  border-color: rgba(251, 68, 67, 0.5);
}

.info {
  flex: 1;
}

.cases_stats {
  width: 35%;
}

.recovered_stats {
  width: 35%;
}

.death_stats {
  width: 35%;
}

.Cases {
  color: #cc1034;
}

.Recovered {
  color: #7dd71d;
}

.Deaths {
  color: #fb4443;
}

.infoBox_cases {
  font-weight: 600;
  font-size: 1.75;
  margin-bottom: 0.5rem;
  width: 100%;
}

.infoBox_total {
  color: #6c757d;
  font-weight: 700 !important;
  font-size: 0.8rem !important;
  margin-top: 15px !important;
}

.info_night > .infoBox > .MuiCardContent-root {
  background-color: black;
}
