.table {
  margin: 20px;
  overflow: scroll;
  height: 400px;
  color: #5a4d4d;
}

.table_night {
  margin: 20px;
  overflow: scroll;
  height: 400px;
  color: white;
}

.table tr:nth-of-type(odd) {
  background-color: #f3f2f8;
}

.table_night tr:nth-of-type(odd) {
  background-color: #15183d;
}

.table tr {
  display: flex;
  justify-content: space-between;
}

.table td {
  padding: 0.5rem;
}

.table_night tr {
  display: flex;
  justify-content: space-between;
}

.table_night td {
  padding: 0.5rem;
}
