* {
  margin: 0;
}
.app_header {
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin-bottom: 20px;
}

.app_stats {
  display: flex;
  justify-content: space-between;
}

body {
  background-color: lightgrey;
}

.App {
  display: flex;
  justify-content: space-evenly;
  padding: 20px;
}

.App_night {
  color: white;
  background-color: #15183d;
  display: flex;
  justify-content: space-evenly;
  padding: 20px;
}

.app_left {
  flex: 0.9;
}

@media (max-width: 990px) {
  .App {
    flex-direction: column;
  }
  .App_night {
    flex-direction: column;
  }
}

.App_night > .app_right > .MuiPaper-root > .MuiCardContent-root {
  color: white;
  background-color: black;
}
/* 
.App_night > .app_left > .app_stats > .MuiPaper-root > .MuiCardContent-root {
  color: white;
  background-color: black;
} */

.menu_night {
  color: white;
  background-color: #15183d;
}

.men_night {
  color: white;
}

#root > div > div.app_left > div.app_header > div > div > div {
  background-color: grey;
}

.night_mode_button {
  font-size: 15px;
  width: 100px;
  height: 50px;
  color: black;
  background-color: grey;
  cursor: pointer;
  border-radius: 10%;
}

.night_mode_button_night {
  font-size: 15px;
  width: 100px;
  height: 50px;
  color: white;
  background-color: grey;
  cursor: pointer;
  border-radius: 10%;
}
